    },
    closeComment(id) {
      this.$refs["comment" + id][0].classList.remove("show-comment");
      this.$refs["comment" + id][0].classList.add("comment");
    },
    publish(id) {
      if (this.input != "") {
        let publish = {
          id: Math.ceil(Math.random() * 100),
          name: "",
          content: this.input,
          time: this.getNowFormatDate2()
        };
        this.recommendedContent[id].comment.push(publish);
        this.input = "";
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },
    getNowFormatDate2() {
      //
      var date = new Date();
      var seperator1 = "-";
      var seperator2 = ":";
      //0+1
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      //1-90
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      //1-90
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      // yyyy-MM-dd HH:mm:ss
      var currentdate =
        date.getFullYear() +
        seperator1 +
        month +
        seperator1 +
        strDate +
        " " +
        date.getHours() +
        seperator2 +
        date.getMinutes() +
        seperator2 +
        date.getSeconds();
      return currentdate;
